.App {
  font-family: Arial, sans-serif;
}

.App-header {
  background-color: #f9f9f9;
  padding: 20px;
  text-align: center;
}

.App-header h1 {
  color: #333;
}

.navbar ul {
  list-style-type: none;
  padding: 0;
  display: flex;
  justify-content: center;
}

.navbar li {
  margin: 0 10px;
}

.navbar button {
  padding: 10px 20px;
  border: none;
  background-color: #eee;
  cursor: pointer;
  transition: background-color 0.2s;
}

.navbar button:hover {
  background-color: #ddd;
}

.navbar button.active {
  background-color: #b5d5f5;
}

.content {
  margin-top: 20px;
}

.social-icons {
  margin-top: 20px;
}

.social-icons a {
  margin: 0 5px;
}
